.popup__container {
    background: #FFFFFF;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    width: 282px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
}

@media screen and (min-width: 1024px) {
    .popup__container {
        width: 430px;
    }
}